.component-tray {
  position:absolute;
  right:0;
  top:0;
  width:25%;
  height:100%;
  background:#f5f5f5;
  font-size:14px;
  .tray__content {
    padding:em(21) em(29);
    height:100%;
    overflow:auto;
  }
  .tray__entry {
    padding-top:1.2em;
    margin-top:1.2em;
    border-top:1px solid $dark;
    &:first-child {
      margin-top:0;
      border-top:none;
    }
  }
  .tray__entry__header {
    h3 {
      font-size:1em;
      margin:0;
    }
  }
  .tray__entry__content {
    h4 {
      margin-top:1.2em;
      margin-bottom:.5em;
      border-bottom:1px solid #ccc;
      font-weight:normal;
      font-style:italic;
      display:inline-block;
    }
  }
  .breakdown {
    list-style:none;
    margin:0;
    li,& {
      padding-left:0;
      margin-left:0;
    }
    li {
      display:flex;
      margin-bottom:.5em;
    }
    .value {
      width:em(70);
      flex:none;
    }
    label {
      flex:auto;
      padding-left:5%;
    }
  }
}