.download-trigger {
  background:$light;
  border-radius:10px;
  padding:em(2);
  display:inline-block;
  cursor:pointer;
  position:relative;
  z-index:10;
  &,svg {
    transition:all .3s ease;
  }
  &:hover {
    background:$purple;
    > svg {
      path {
        fill:$light;
      }
    }
    .download__options {
      pointer-events:all;
      opacity:1;
    }
  }
  > svg {
    height:em(30);
    width:em(36);
    path {
      fill:$purple;
    }
  }
  .download__options {
    position:absolute;
    bottom:100%;
    left:50%;
    transform:translateX(-50%);
    background:$light;
    opacity:0;
    pointer-events:none;
    transition:opacity .3s ease;
    border-radius:10px;
    box-shadow:5px 5px 25px #00000029;
    margin-bottom:1em;
    display:flex;
    width:auto;
    padding-left:em(10);
    padding-right:em(10);
    &:after {
      content:"";
      border-style:solid;
      border-width:1em;
      border-color:$light transparent transparent;
      position:absolute;
      top:100%;
      left:50%;
      transform:translateX(-50%);
    }
  }
  .download__option {
    color:$dark;
    font-size:.75em;
    padding:em(20);
    line-height:1em;
    text-align:center;
    text-transform:uppercase;
    font-weight:900;
    label {
      cursor:pointer;
    }
    svg {
      height:2em;
      width:auto;
      margin-bottom:.5em;
      path {
        fill:$dark;
      }
    }
    &:hover {
      color:$purple;
      svg {
        path {
          fill:$purple;
        }
      }
    }
  }
}